import { DecimalPipe } from "@angular/common";
import { Pipe, PipeTransform } from "@angular/core";
import * as dayjs from "dayjs";

@Pipe({
  name: 'dynamicPipe',
  standalone: true
})
export class DynamicPipe implements PipeTransform {

  constructor(private decimalPipe: DecimalPipe) {}

  transform(value: any, pipeName: string, pipeArgs?: any[]): any {
    switch (pipeName) {
      case 'decimal':
        return this.decimalPipe.transform(value, '1.0-2');
      
      case 'time':
        return dayjs(value).format('DD/MM/YYYY HH:mm');

      case 'array':
        return `<span class="${pipeArgs?.find(item => item.id === value)?.class}">${pipeArgs?.length && pipeArgs?.find(item => item.id === value)?.value || ''}</span>`;

      default:
        return value; // Trả về giá trị mặc định nếu không có pipe phù hợp
    }
  }
}
