import { Directive, ElementRef, Input, OnInit, TemplateRef, ViewContainerRef } from '@angular/core';
import { PermissionService} from '../services/authenticate/permission.service';

@Directive({
  selector: '[icomLibsHasPermissionDirective]',
  standalone: true
})
export class IcomLibsHasPermissionDirective implements OnInit {
  @Input() action!: string;

  constructor(
    private el: ElementRef, 
    private permissionService: PermissionService
  ) {}

  ngOnInit() {
    if (!this.permissionService.getPermission(this.action)) {
      this.el.nativeElement.style.setProperty('display', 'none', 'important');
    }
  }
}

