export interface TableAction {
  name: string;
  callback: (data: any) => void;
}

export interface TableColumn {
  title: string;
  key: string;
  pipe?: PipeTransform;
  pipeArgs?: any[];
  type?: 'text' | 'action';
  actions?: TableAction[];
}


@Component({
  selector: 'icom-libs-core-components-table',
  templateUrl: './table.component.html',
  standalone: true,
  changeDetection: ChangeDetectionStrategy.Default,
  imports: [
    NzTableModule,
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NzFormModule,
    NgForOf,
    NgSwitch,
    NgSwitchCase,
    NzTypographyModule,
    NzIconModule,
    NzModalModule,
    NzSelectModule,
    FormsModule,
    NzDropDownModule,
    NgIf,
    JsonPipe,
    DynamicPipe,
    NzSwitchModule,
    ShowTooltipOnOverflowDirective,
    NzToolTipModule,
    IcomLibsHasPermissionDirective,
    NzSwitchModule,
    NzImageModule
  ],
  providers: [DecimalPipe],
  styles: [`
  nz-table {
    table:has(nz-embed-empty) {
      height: 100%;
    }
    overflow: hidden;
    nz-spin {
      height: 100%;
      .ant-spin-container {
        height: calc(100% - 50px) !important;
        .ant-table {
          height: 100%;
          .ant-table-container {
            height: 100%;
            overflow: hidden;
            .ant-table-body {
              overflow-x: scroll;
              overflow-x: hidden;
              height: calc(100% - 50px);
            }
          }
        }
      }
    }
  }
`]
})

export class IcomLibsCoreTableComponent implements OnInit {
  protected dataSet: any[] = [];
  protected isLoading = true;
  protected pagination: any;
  protected options: FormlyFormOptions = {};
  protected lodash = _;

  @Input() columns: any[];
  @Input() loadFn!: (params: any) => any;
  @Input() fields!: FormlyFieldConfig[];
  @Input() model!: any;
  @Input() form!: FormGroup;
  @Input() functionParse!: (params: any) => any;
  @Input() functionExportFile!: (params: any) => any;
  @Input() limit!: number;
  @Input() displayExportFile!: boolean;
  @Input() ignoreButtonAdd!: boolean;

  @Output() icomFilterChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() icomAdd: EventEmitter<any> = new EventEmitter<any>();
  @Output() icomFunctionControl: EventEmitter<any> = new EventEmitter<any>();
  
  protected IComMessageService = inject(IComMessageService);
  protected cdr = inject(ChangeDetectorRef);

  constructor() {
    this.columns = [];
    this.options = {
      formState: {
        hidden: false
      },
    };
  }

  ngOnInit(): void {
    this.fetchData(1, this.limit || 10);
    this.icomFunctionControl.emit({
      reset: () => {
        this.fetchData();
      },
      addItem: (data: any) => this.addItem(data),
      mergeItem: (key: string, data: any) => this.mergeItem(key, data),
    })
  }

  protected fetchData(page: number = 1, limit: number = 10) {
    this.isLoading = true;
    let params = {
      limit: limit,
      page: page,
      ...this.model
    };
    this.model?.start_time && (params = {
      ...params,
      start_time: this.model.start_time ? dayjs(this.model.start_time).format('YYYY-MM-DD') : '',
    });
    
    console.log(this.lodash.get(params, 'limit'));

    this.model?.end_time && (params = {
      ...params,
      end_time: this.model.end_time ? dayjs(this.model.end_time).format('YYYY-MM-DD') : '',
    });
    this.loadFn && lastValueFrom(this.loadFn(params)).then((result: any) => {
      if (result && result.data && result.data.items) {

        this.dataSet = result?.data?.items || [];
        this.functionParse && (this.dataSet = this.functionParse(result.data.items));
        this.pagination = result.data.pagination;
        this.isLoading = false;
        this.cdr.detectChanges();
        return;
      }
      this.dataSet = [];
      this.pagination = result.data.pagination;
      this.isLoading = false;
      console.log(this.pagination);
      this.cdr.detectChanges();

    }).catch((err) => {
      this.dataSet = [];
      this.pagination = undefined;
      this.isLoading = false;
      this.cdr.detectChanges();
    });
  }

  protected onSearch() {
    this.fetchData(1);
    this.icomFilterChange.emit(this.model);
  }

  protected onPageChange(page: number) {
    this.fetchData(page, this.pagination.limit);
  }

  protected handlerSearch(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.fetchData(1, this.limit || 10);
  }

  protected showModal(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.icomAdd.emit(true);
  }

  protected handlerReset(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.model = {};
    this.form.reset();
    this.fetchData(1, this.limit || 10);
  }

  protected handlerExport(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.functionExportFile(this.model);
  }

  protected handlerToggleCollapse(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.options.formState.hidden = !this.options.formState.hidden;
  }

  protected onPageSizeChange(page: number) {
    this.limit = page;
    this.fetchData(1, this.limit || 10);
  }

  protected mergeItem(key: string, data: any) {
    const index = this.dataSet.findIndex((item) => item[key]);
    if (index !== -1) {
      this.dataSet[index] = { ...data };
      this.cdr.detectChanges();
    }
  }

  protected addItem(data: any) {
    this.dataSet.unshift(data);
    this.cdr.detectChanges();
  }
}
